import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    meta: {
      title: "首頁"
    },
    component: Home
  },
  {
    path: "/about",
    name: "About",
    meta: {
      title: "關於"
    },
    component: () => import("../views/About.vue")
  },
  {
    path: "/video",
    name: "Video",
    meta: {
      title: "視訊"
    },
    component: () => import("../views/Video.vue")
  },
  {
    path: "/environment",
    name: "Environment",
    meta: {
      title: "環境"
    },
    component: () => import("../views/Environment.vue")
  }
];

const router = new VueRouter({
  mode: "hash",
  base: process.env.BASE_URL,
  routes
});

router.beforeEach((to, from, next) => {
  if (to.meta.title) {
    document.title =
      "論小型自動化自娛娛人歡樂盆栽實行遠種植之開開心心零距離培養計畫 - " +
      to.meta.title;
  }
  next();
});

export default router;
